import Vue from 'vue'
import VueI18n from 'vue-i18n'

import enLang from './en'
import idLang from './id'
import store from '../store'

import { LANG_ID } from '@/fixtures/Fixtures'

Vue.use(VueI18n);

export default new VueI18n({
  locale: store.getters['Auth/language'],
  fallbackLocale: LANG_ID,
  messages: {
    'en': enLang,
    'id': idLang
  }
});
