import CryptoJS from 'crypto-js'

import { ENC_LANG, ENC_LANG_ID } from '@/fixtures/EncryptedFixtures'
import { CRYPTO_KEY } from '@/fixtures/Hash'

const namespaced = true

const state = {
  language: localStorage.getItem(ENC_LANG) || CryptoJS.AES.encrypt(ENC_LANG_ID, CRYPTO_KEY).toString()
}

const getters = {
  language: state => CryptoJS.AES.decrypt(state.language, CRYPTO_KEY).toString(CryptoJS.enc.Utf8)
}

const mutations = {
  SET_LANGUAGE: (state, payload) => {
    state.language = payload
    localStorage.setItem(ENC_LANG, payload)
  }
}

const actions = {
  setLanguage({ commit }, payload) {
    commit('SET_LANGUAGE', CryptoJS.AES.encrypt(btoa(payload), CRYPTO_KEY).toString())
  }
}

export default { namespaced, state, getters, mutations, actions }
